import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import FolderIcon from '@material-ui/icons/Folder';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from "@material-ui/icons/Edit"

import StackGrid from "react-stack-grid";


import firebase from "../firebase"
import { useState, useEffect } from "react"

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    maxWidth: 752,
  },
  demo: {
    backgroundColor: theme.palette.background.paper,
  },
  title: {
    margin: theme.spacing(4, 0, 2),
  },
}));

export default function Collection() {

  const classes = useStyles()
  const folderData = useDetails()

  const collections = folderData.map(folder =>
    <Grid item key={folder.folder_id}>
      <ListItem button >
        <ListItemAvatar>
          <Avatar>
            <FolderIcon />
          </Avatar>
        </ListItemAvatar>
        <ListItemText
          primary={folder.folder_name}
          secondary={folder.folder_desc}
        />
        <ListItemSecondaryAction>
          <IconButton edge="end" aria-label="edit">
            <EditIcon />
          </IconButton>
          <IconButton edge="end" aria-label="delete"
            onClick={e => deleteCollection(folder.folder_id)}>
            <DeleteIcon />
          </IconButton>
        </ListItemSecondaryAction>
      </ListItem>
    </Grid>
  )

  return (
    <>
      <div>
        <StackGrid columnWidth="100%">
          <div className={classes.demo}>
            <List dense>
              {collections}
            </List>
          </div>
        </StackGrid>
      </div>
    </>
  );
}

function deleteCollection(id) {

	firebase.firestore()
		.collection("collections")
		.doc(id)
		.delete()
		.then(() =>
			console.log("Success")
		).catch((err) =>
			console.error("Error removing document: ", err)
		)
}

function useDetails() {
	const [folderDetails, setFolderDetails] = useState([])

	useEffect(() => {
		const unsubscribe = firebase
			.firestore()
			.collection("collections")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					folder_id: doc.id,
					...doc.data()
				}))
				setFolderDetails(newDetails)
			})
        
        return () => unsubscribe()
    }, [])
    
    return folderDetails
}